    getCurrentTime() {
      let myDate = new Date();
      let year = myDate.getFullYear(); //
      let month = myDate.getMonth() + 1; //
      let day = myDate.getDate(); //
      let days = myDate.getDay();
      let hh = myDate.getHours(); //
      let mm = myDate.getMinutes(); //
      // let h=''
      // let m=''
      let weekList = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ];
      if (hh < 10) {
        hh = "0" + hh;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      let week = weekList[days];
      // console.log(year + "" + month + "" + day + "  " + week);
      this.curDate = `${year}${month}${day}`;
      this.curWeek = week;
      this.curTime = `${hh}:${mm}`;
    },
