    //
    getClassification() {
      return new Promise((reslove, reject) => {
        let apiParams = {
          appKey: "c9406731-50bc-4e23-b3f5-b7e7d3093875",
          time: "1633773099169",
          sign: "e6kyRzVKYrLOwryeXNlgmg==",
        };
        let p = {
          dataGuid: "2bd7a7ae-fc24-4244-87c0-107b40684008",
          ...apiParams,
        };
        let p1 = {
          dataGuid: "9f16e2d2-2995-4d05-9619-5916e077d96a",
          ...apiParams,
        };
        this.$http.openDataView(p).then((res) => {
          let { Data } = res.data;

          let xData1 = Data.map((e) => {
            return e.TOWNSHIPNUM_TOWNNAME;
          });
          let yData1 = Data.map((e) => {
            return e.NUMBER;
          });
          let yData2 = Data.map((e) => {
            return e.NUMBER1;
          });
          let yData3 = Data.map((e) => {
            return e.NUMBER2;
          });
          this.rightEchartsData.classification = {
            xData1,
            yData1,
            yData2,
            yData3,
          };
          reslove();
        });
        this.$http.openDataView(p1).then((res) => {
          let { Data } = res.data;
          let { NUMBER, NUMBER1, NUMBER2 } = Data[0];
          this.FLSCList.forEach((e) => {
            if (e.name == "") {
              e.value = NUMBER;
            } else if (e.name == "") {
              e.value = NUMBER1;
            } else {
              e.value = NUMBER2;
            }
          });
        });
      });
    },
